html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--background-color);
  color: var(--text-dark);
  margin-block-end: 50vh;
}

input,
button {
  font-family: var(--font-heading);
}

img {
  display: block;
  width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0;
}

h1 {
  font-size: 3.75rem;
  font-size: clamp(2.625rem, 1.77rem + 3.64vw, 4.5rem);
  line-height: 1;
  margin-block-end: 1.25rem;
}

p {
  font-size: 1.125rem;
  line-height: 1.3;
  margin-top: 0;
}
p.medium {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.14rem + 0.48vw, 1.5rem);
}
p.large {
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 1.39rem + 0.48vw, 1.75rem);
  margin-block-end: 2.5rem;
}

a:visited,
a:active {
  text-decoration: none;
}

a {
  text-decoration: underline;
}

p {
  font-size: 1.125rem;
  line-height: 1.3;
  margin-top: 0;
}
p.medium {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.14rem + 0.48vw, 1.5rem);
}
p.large {
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 1.39rem + 0.48vw, 1.75rem);
  margin-block-end: 2.5rem;
}

:root {
  --accurate: hsl(85, 54%, 51%);
  --primaryGreenMed: hsl(85, 52%, 41%);
  --primaryGreenDark: hsl(85, 52%, 31%);
  --primaryGrey: hsl(220, 2%, 35%);
  --primaryLtGrey: hsl(210, 3%, 75%);
  --ltGrey: hsl(0, 0%, 62%);
  --medGrey: hsl(0, 0%, 44%);
  --dkGrey: hsl(0, 0%, 25%);
  --black:hsl(0, 0%, 6%);
  --white: hsl(240, 100%, 99%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
  --font-heading: "Roboto Slab", Helvetica, Arial, sans-serif; /* heading font */
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
  padding-block: 60px;
}
@media (min-width: 56.25em) {
  .wrapper {
    padding-block: 60px;
  }
}

.fb-wrapper {
  display: grid;
  grid-template-columns: [fb-start] 1fr [content-start] min(100% - 3rem, 75rem) [content-end] 1fr [fb-end];
}
.fb-wrapper > * {
  grid-row: 1/2;
}
.fb-wrapper__full {
  grid-column: fb-start/fb-end;
  align-self: end;
}
.fb-wrapper__content {
  grid-column: content-start/content-end;
}

.footer {
  padding: 1.25rem;
}