@use '../util' as u;

h1,
h2,
h3 {
	font-weight: 700;
	margin-top: 0;
}

h1 {
	font-size: u.rem(60);
	font-size: clamp(
		u.rem(42),
		1.77rem + 3.64vw,
		u.rem(72)
	);
	line-height: 1;
	margin-block-end: u.rem(20);
}

p {
	font-size: u.rem(18);
	line-height: 1.3;
	margin-top: 0;

	&.medium {
		font-size: u.rem(20);
		font-size: clamp(u.rem(20), 1.14rem + 0.48vw, u.rem(24));
	}

	&.large {
		font-size: u.rem(24);
		font-size: clamp(u.rem(24), 1.39rem + 0.48vw, u.rem(28));
		margin-block-end: u.rem(40);
	}
}

a:visited,
a:active {
	text-decoration: none;
}

a {
	text-decoration: underline;
}

p {
	font-size: u.rem(18);
	line-height: 1.3;
	margin-top: 0;

	&.medium {
		font-size: u.rem(20);
		font-size: clamp(u.rem(20), 1.14rem + 0.48vw, u.rem(24));
	}

	&.large {
		font-size: u.rem(24);
		font-size: clamp(u.rem(24), 1.39rem + 0.48vw, u.rem(28));
		margin-block-end: u.rem(40);
	}
}